export default {
	data() {
		return {
			LabelEntity:{
				LocationCode:'',
				PickOrderCode:'',
				ReelId:''
			},
			locationFoucs:true,
			labelFoucs:false,
			PickOrderCode:''
		}
	},
	onLoad:function(e){
		this.PickOrderCode=e.PickOrderCode
		this.LabelEntity.PickOrderCode=e.PickOrderCode
	},
	methods: {
		OnEnter(){
			if(this.LabelEntity.LocationCode!='' || this.LabelEntity.ReelId!=''){
				uni.request({
					url: this.apiUrl + 'PreparationView/GetReelIdByLocationCodeForPick',
					method: "get",
					data:{PickOrderCode:this.LabelEntity.PickOrderCode,ReelId:this.LabelEntity.ReelId, LocationCode:this.LabelEntity.LocationCode},
					success: res => {
						var resData = JSON.parse(res.data.Content)
						if (resData.Success) {
							console.log(resData)
							this.LabelEntity =resData.Data
						} else {
							uni.showModal({
								content: resData.Message,
								showCancel: false
							});
							return
						}
				
					},
					fail: () => {
				
					},
					complete: () => {
				
					}
				})
								
			}
		},
		Back(){
			uni.navigateBack({
				
			});
		},
		ConfirmCancel(){
			uni.request({
				url: this.apiUrl + 'PreparationView/CancelPickMaterialLabel',
				method: "post",
				data: this.LabelEntity,
				success: res => {
					var resData = JSON.parse(res.data.Content)
					if (resData.Success) {
		                uni.showModal({
							content: resData.Message,
							showCancel: false
						});
						this.LabelEntity={
							PickOrderCode:this.PickOrderCode,
							LocationCode:'',
							ReelId:''
						}
					} else {
						uni.showModal({
							content: resData.Message,
							showCancel: false
						});
						return
					}
			
				},
				fail: () => {
			
				},
				complete: () => {
			
				}
			})
				
		}
		
	}
}
